.popup__content {
  background-color: white;
  color: black;
  width: 430px;

  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
  padding: 34px 36px 37px 36px;
  box-sizing: border-box;
} 

@media screen and (max-width: 550px) {
  .popup__content {
    width: 282px;
  
    padding: 25px 22px 25px 22px;
  }
}